.popup__btn-save {
    width: 100%;
    min-height: calc((100vw - 320px) / (1280 - 320) * (50 - 46) + 46px);
    margin-top: 50px;
    border: none;
    border-radius: 2px;
    background-color: #000;
    color: #fff;
    font-size: calc((100vw - 320px) / (1280 - 320) * (18 - 14) + 14px);
    line-height: calc((100vw - 320px) / (1280 - 320) * (22 - 17) + 17px);
    transition: opacity 0.2s linear;
}

.popup__btn-save:hover {
    opacity: 0.8;
    cursor: pointer;
}

@media screen and (min-width: 1280px) {
    .popup__btn-save {
        min-height: 50px;
        font-size: 18px;
        line-height: 22px;
    }
}
